.projectBlock {
  min-height: 60vh;
  background-color: #131f33;
  padding-top: 80px;
  padding-bottom: 80px;

  .projectsContainer {
    flex-direction: column;
    justify-content: flex-end;
    align-content: flex-end;

    .projects {
      width: 100%;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      @media screen and (max-width: 625px) {
        justify-content: center;
      }
    }
  }
}


