.project {
    width: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.icon {
    height: 200px;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: center;
    position: relative;
}

.icon::before {
    position: absolute;
    content: "";
    background-color: black;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0.3;
}

.button {
    align-items: center;
    justify-items: center;
    background-color: #8cc090;
    position: relative;
    z-index: 1;
}

.title {
    font-family: 'Baloo Bhaijaan', cursive;
    color: #ffffff;
    font-style: normal;
    font-weight: 400;
}

.text {
    font-size: 16px;
    font-weight: normal;
    line-height: 2;
    color: #8b98af
}

.projectInfo {
    padding: 20px 20px;
}