.skillsBlock {
    min-height: 100vh;
    background-color: #131f33;
}

.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.title {
    background-color: #eeeef5;
}

.skills {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

@media screen and (max-width: 626px){
    .skills{
        justify-content: center;
    }
}